import Toast from '../../wxcomponents/vant/toast/toast';
const db = wx.cloud.database();
export default{
	data(){
		return{
			avatarUrl:'../../static/icon/avatar.png',//头像
			nickName:'请授权登录',
			token:'',
			buttonShow: false,
			appoinList: [],
			classNum: 0,
			worksNum: 0
		}
	},
	async onLoad() {
		const openid = uni.getStorageSync('openid')
		await db.collection('works').where({_openid : openid}).get().then(res => {
			this.worksNum = res.data.length
		})
	},
	async onShow() {
		await wx.cloud.callFunction({
		  // 要调用的云函数名称
		  name: 'getClassTable'
		}).then(res => {
		  // this.list = res.result.data.classId
		  this.classNum = res.result.result.data.length
		}).catch(err => {
		  // handle error
		})
		this.getUserInfo()
		this.token = uni.getStorageSync('token');
		this.isLogin = uni.getStorageSync('isLogin')
		const openid = uni.getStorageSync('openid')
		db.collection('appointment').where({_openid:openid}).get().then(res => {
			// res.data 包含该记录的数据
			this.appoinList = res.data;
			console.log(res);
		});
	},
	methods:{
		getUserInfo(e) {
		    // console.log(e)
		    // 获取用户信息
			let that = this
		    wx.getSetting({
		      success(res) {
		        // console.log("res", res)
		        if (res.authSetting['scope.userInfo']) {
		          console.log("已授权=====")
				  that.buttonShow = false;
		          // 已经授权，可以直接调用 getUserInfo 获取头像昵称
		          wx.getUserInfo({
		          	success: function (res) {
		          		//获取到的微信头像 昵称 性别
		          		that.nickName = res.userInfo.nickName;
		          		that.avatarUrl = res.userInfo.avatarUrl;
		          		
		          	}
		          });
		        } else {
		          console.log("未授权=====")
				  that.buttonShow = true;
		        }
		      }
		    })
		  },
		  toDetail(id){
			  uni.navigateTo({
			  	url:"/pages/appoin/detail?_id=" + id
			  })
		  },
		  joinClass(){
			  uni.navigateTo({
			  	url:"/pages/join-class/join-class"
			  })
		  },
		  choseImg(){
			  let nickName = ''
			  if(this.nickName == '请授权登录'){
			  	nickName = '无名'
			  }else{
			  	nickName = this.nickName
			  }
			  wx.chooseImage({
			    sizeType: ['original', 'compressed'],
			    sourceType: ['album', 'camera'],
			    success (res) {
			      // tempFilePath可以作为img标签的src属性显示图片
				  wx.showLoading({
				    title: '上传中',
				  });
				  let filePath = res.tempFilePaths[0];
				  const name = Math.random() * 1000000;
				  const cloudPath = 'works/' + name + filePath.match(/\.[^.]+?$/)[0]
				  wx.cloud.uploadFile({
				    cloudPath,
				    filePath, // 文件路径
				  }).then(res => {
					const data = {
						fileID: res.fileID,
						nickName
					}
				    db.collection('works').add({
						data
					}).then(res => {
						wx.hideLoading()
						Toast('上传成功')
						this.worksNum++
					})
				    console.log(res.fileID)
				  }).catch(error => {
				    wx.hideLoading()
				    Toast('上传失败')
				  })
			    }
			  })
		  }
	}
}
